<template>
  <div class="login" style="padding-bottom:40px;">
    <h2 class="center-heading color cursive" >Login to Your Account</h2>
    <h4 class="center-heading color Island">Welcome to the infinity of knowledge</h4>
    <div class="center content-inputs">
      
      <vs-alert v-model="error" color="danger" style="margin-bottom:20px;">
        <template #title>
          Error
        </template>
       {{error_message}}
      </vs-alert>


      <vs-input style="align-items:center" state="warn" warn v-model="userid" class="color Inconsolata" placeholder="Enter Your User Id  ">
        <template #icon>
           <span class="material-icons-outlined">account_circle</span>
        </template>
      </vs-input>

      <vs-input type="password" state="warn" icon-after style="align-items:center; padding-top:20px;" warn v-model="pin" class="color Inconsolata" placeholder="Enter Your Secret Pin">
        <template #icon>
           <span class="material-icons-outlined">lock</span>
        </template>
      </vs-input>
     
      <vs-row w="12">
        <vs-col :w="x">
          
        </vs-col>
        <vs-col :w="w">
           <vs-checkbox class="color cursive" style="text-align:center; vertical-align:middle; padding-top:20px;" warn gradient v-model="terms">
            <h5>I agree to all the terms & condition</h5>
           </vs-checkbox>
        </vs-col>
        <vs-col :w="y">
          
        </vs-col>
      </vs-row>
  
    </div>
    <div style="padding-top:40px;padding-bottom:20px">
     <vs-button @click="login" warn gradient :active="active == 3" style="jusitfy-items:center;" class="cursive btn_center"> Login &nbsp; <img src="../assets/arrow_forward_white_18dp.svg"> </vs-button>
    </div>

  </div>
</template>

<script>

export default{
  data: () => ({
    userid:'',
    terms:false,
    active:0,
    pin:'',
    error:0,
    error_message:'',
    w: 8,
    x:3,
    y:1,
    windowWidth: 721,
    time:3000,
    progress:0
  }),
  methods:{
    width(){
     this.windowWidth = window.innerWidth;
     console.log(this.windowWidth);
     if(this.windowWidth>990){
        this.x=5;
        this.w=4;
        this.y=3;
     }
    },
    login(){
      console.log(this.terms);
      if(this.terms == true){
        console.log(this.userid);
        console.log(this.pin);
        this.active = 3;
      }
      else{
        this.error_message="Do agree with our terms and conditions";
        this.error=1;
        console.log(this.error);
      }
    }
  },
  watch:{
    $route (){
      this.width();
    },
    error(val) {
          if(val) {
            var interval = setInterval(() => {
              this.progress++
            }, this.time / 100);

            setTimeout(() => {
              this.error = 0;
              clearInterval(interval)
              this.progress = 0
            }, this.time);
          }
        }
  },
  created(){
    this.width();
  }
}
</script>


